.app_wrapper {
    display: grid;
    grid-template-areas: "h h " "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    /* grid-gap: 10px; */
    width: 1200px;
    margin: 0 auto;
}

.app_wrapper_content {
    grid-area: c;
    background-color: rgb(179, 180, 204);
    border-radius: 40px;
    border: rgb(245, 34, 6) 1px solid;
}